                <el-checkbox label="uppercase">å¤§å†™å­—æ¯ (A-Z)</el-checkbox>
                <el-checkbox label="lowercase">å°å†™å­—æ¯ (a-z)</el-checkbox>
                <el-checkbox label="numbers">æ•°å­— (0-9)</el-checkbox>
                <el-checkbox label="symbols">ç¬¦å· (!@#$%^&*)</el-checkbox>
