                placeholder="è¾“å…¥è¦æ’é™¤çš„å­—ç¬¦ï¼Œå¦‚ï¼š0O1l"
                @input="generatePassword"
              ></el-input>
              <div class="help-text">å¸¸è§æ˜“æ··æ·†å­—ç¬¦ï¼š0O1l (é›¶æ¬§ä¸€L)</div>
            </el-form-item>

            <el-form-item label="ç”Ÿæˆæ•°é‡">
              <el-input-number 
