              ></el-input-number>
            </el-form-item>

            <el-form-item label="é«˜çº§é€‰é¡¹">
              <el-checkbox v-model="form.noSimilar" @change="generatePassword">
                æ’é™¤ç›¸ä¼¼å­—ç¬¦ (0O1l)
              </el-checkbox>
              <el-checkbox v-model="form.noAmbiguous" @change="generatePassword">
                æ’é™¤æ­§ä¹‰å­—ç¬¦ ({}[]()\\~,;.&lt;&gt;)
              </el-checkbox>
              <el-checkbox v-model="form.mustIncludeAll" @change="generatePassword">
                å¿…é¡»åŒ…å«æ‰€æœ‰é€‰ä¸­ç±»å‹
              </el-checkbox>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="generatePasswords">
