            <el-descriptions-item label="å¼ºåº¦è¯„åˆ†">{{ strengthScore }}/100</el-descriptions-item>
            <el-descriptions-item label="ç ´è§£æ—¶é—´">{{ crackTime }}</el-descriptions-item>
            <el-descriptions-item label="ç†µå€¼">{{ entropy }} bits</el-descriptions-item>
            <el-descriptions-item label="å¯èƒ½ç»„åˆ">{{ combinations }}</el-descriptions-item>
          </el-descriptions>
