<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'

export default {
  name: 'PasswordGeneratorView',
  metaInfo: {
    title: 'å¯†ç ç”Ÿæˆå™¨ - å®‰å…¨å¯†ç ç”Ÿæˆå·¥å…·',
    meta: [
      { name: 'description', content: 'åœ¨çº¿å¯†ç ç”Ÿæˆå™¨ï¼Œç”Ÿæˆå®‰å…¨å¯é çš„éšæœºå¯†ç ï¼Œæ”¯æŒè‡ªå®šä¹‰é•¿åº¦å’Œå­—ç¬¦ç±»å‹ï¼Œæä¾›å¯†ç å¼ºåº¦åˆ†æã€‚' },
      { name: 'keywords', content: 'å¯†ç ç”Ÿæˆå™¨,éšæœºå¯†ç ,å®‰å…¨å¯†ç ,å¯†ç å¼ºåº¦,åœ¨çº¿å·¥å…·' }
    ]
  },
  data() {
    return {
      form: {
        length: 16,
        charTypes: ['uppercase', 'lowercase', 'numbers', 'symbols'],
        excludeChars: '',
        count: 5,
        noSimilar: false,
        noAmbiguous: false,
        mustIncludeAll: true
      },
      generatedPasswords: [],
      strengthScore: 0,
      strengthText: '',
      strengthClass: '',
      strengthPercentage: 0,
      crackTime: '',
      entropy: 0,
      combinations: ''
    }
  },
  mounted() {
    this.generatePasswords()
  },
  methods: {
    generatePasswords() {
      this.generatedPasswords = []
      for (let i = 0; i < this.form.count; i++) {
        this.generatedPasswords.push(this.generatePassword())
      }
      if (this.generatedPasswords.length > 0) {
        this.analyzeStrength(this.generatedPasswords[0])
      }
    },

    generatePassword() {
      const charset = this.buildCharset()
      if (charset.length === 0) {
        this.$message.warning('è¯·è‡³å°‘é€‰æ‹©ä¸€ç§å­—ç¬¦ç±»å‹')
        return ''
      }

      let password = ''
      
      // å¦‚æœå¿…é¡»åŒ…å«æ‰€æœ‰ç±»å‹ï¼Œå…ˆç¡®ä¿æ¯ç§ç±»å‹è‡³å°‘æœ‰ä¸€ä¸ªå­—ç¬¦
      if (this.form.mustIncludeAll) {
        const charSets = this.getCharSets()
        for (const type of this.form.charTypes) {
          if (charSets[type]) {
            const chars = this.filterChars(charSets[type])
            if (chars.length > 0) {
              password += chars[Math.floor(Math.random() * chars.length)]
            }
          }
        }
      }

      // å¡«å……å‰©ä½™é•¿åº¦
      while (password.length < this.form.length) {
        password += charset[Math.floor(Math.random() * charset.length)]
      }

      // æ‰“ä¹±å¯†ç å­—ç¬¦é¡ºåº
      return this.shuffleString(password)
    },

    buildCharset() {
      const charSets = this.getCharSets()
      let charset = ''

      for (const type of this.form.charTypes) {
        if (charSets[type]) {
          charset += this.filterChars(charSets[type])
        }
      }

      return charset
    },

    getCharSets() {
      return {
        uppercase: 'ABCDEFGHIJKLMNOPQRSTUVWXYZ',
        lowercase: 'abcdefghijklmnopqrstuvwxyz',
        numbers: '0123456789',
        symbols: '!@#$%^&*()_+-=[]{}|;:,.<>?'
      }
    },

    filterChars(chars) {
      let filtered = chars

      // æ’é™¤ç”¨æˆ·æŒ‡å®šçš„å­—ç¬¦
      if (this.form.excludeChars) {
        for (const char of this.form.excludeChars) {
          filtered = filtered.replace(new RegExp(char.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'), 'g'), '')
        }
      }

      // æ’é™¤ç›¸ä¼¼å­—ç¬¦
      if (this.form.noSimilar) {
        filtered = filtered.replace(/[0O1l]/g, '')
      }

      // æ’é™¤æ­§ä¹‰å­—ç¬¦
      if (this.form.noAmbiguous) {
        filtered = filtered.replace(/[{}[\]()\\~,;.<>]/g, '')
      }

      return filtered
    },

    shuffleString(str) {
      return str.split('').sort(() => Math.random() - 0.5).join('')
    },

    analyzeStrength(password) {
      if (!password) return

      const charset = this.buildCharset()
      this.entropy = Math.log2(Math.pow(charset.length, password.length))
      this.combinations = Math.pow(charset.length, password.length).toExponential(2)

      // è®¡ç®—å¼ºåº¦è¯„åˆ†
      let score = 0
      
      // é•¿åº¦è¯„åˆ† (0-25åˆ†)
      score += Math.min(password.length * 2, 25)
      
      // å­—ç¬¦ç±»å‹è¯„åˆ† (0-25åˆ†)
      const hasUpper = /[A-Z]/.test(password)
      const hasLower = /[a-z]/.test(password)
      const hasNumber = /[0-9]/.test(password)
      const hasSymbol = /[^A-Za-z0-9]/.test(password)
      const typeCount = [hasUpper, hasLower, hasNumber, hasSymbol].filter(Boolean).length
      score += typeCount * 6.25

      // ç†µå€¼è¯„åˆ† (0-50åˆ†)
      score += Math.min(this.entropy * 2, 50)

      this.strengthScore = Math.round(score)
      this.strengthPercentage = this.strengthScore
      
      // è®¾ç½®å¼ºåº¦æ–‡æœ¬å’Œæ ·å¼
      if (score < 30) {
        this.strengthText = 'å¼±'
        this.strengthClass = 'weak'
      } else if (score < 60) {
        this.strengthText = 'ä¸­ç­‰'
        this.strengthClass = 'medium'
      } else if (score < 80) {
        this.strengthText = 'å¼º'
        this.strengthClass = 'strong'
      } else {
        this.strengthText = 'éå¸¸å¼º'
        this.strengthClass = 'very-strong'
      }

      // è®¡ç®—ç ´è§£æ—¶é—´
      this.calculateCrackTime()
    },

    calculateCrackTime() {
      const combinations = Math.pow(this.buildCharset().length, this.form.length)
      const guessesPerSecond = 1e9 // å‡è®¾æ¯ç§’10äº¿æ¬¡å°è¯•
      const secondsToCrack = combinations / (2 * guessesPerSecond) // å¹³å‡éœ€è¦å°è¯•ä¸€åŠçš„ç»„åˆ

      if (secondsToCrack < 60) {
        this.crackTime = 'å°äº1åˆ†é’Ÿ'
      } else if (secondsToCrack < 3600) {
        this.crackTime = `${Math.round(secondsToCrack / 60)}åˆ†é’Ÿ`
      } else if (secondsToCrack < 86400) {
        this.crackTime = `${Math.round(secondsToCrack / 3600)}å°æ—¶`
      } else if (secondsToCrack < 31536000) {
        this.crackTime = `${Math.round(secondsToCrack / 86400)}å¤©`
      } else if (secondsToCrack < 31536000000) {
        this.crackTime = `${Math.round(secondsToCrack / 31536000)}å¹´`
      } else {
        this.crackTime = 'æ•°åƒå¹´ä»¥ä¸Š'
      }
    },

    async copyPassword(password) {
      try {
        const success = await ClipboardUtil.copyText(password)
        if (success) {
          this.$message.success('å¯†ç å·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },

    async copyAllPasswords() {
      try {
        const allPasswords = this.generatedPasswords.join('\n')
        const success = await ClipboardUtil.copyText(allPasswords)
        if (success) {
          this.$message.success('æ‰€æœ‰å¯†ç å·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    }
  }
}
</script>

